import axios from '@axios'

export default {
  namespaced: true,
  state: {
    talleresSegunDeporte: [],
    taller_global_id: null,
    tituloModal: '',
    tituloBoton: '',
    guardarEditar: true,
  },
  getters: {},
  mutations: {
    listarTalleresSegunDeporte(state, valor) {
      state.talleresSegunDeporte = valor.data
    },
  },
  actions: {
    fetchTalleres(ctx, params) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-cultural-deportivo/${params.codigo}/global/${params.id}/taller`)
          .then(response => { ctx.commit('listarTalleresSegunDeporte', response.data); resolve(response) })
          .catch(error => reject(error))
      })
    },
    addCursoTaller(ctx, data) {
      return new Promise((resolve, reject) => {
        axios
          .post(`/servicio-deportivo/taller-global/${ctx.state.taller_global_id}/taller`, data)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    updateCursoTaller(ctx, data) {
      return new Promise((resolve, reject) => {
        axios
          .put(`/servicio-deportivo/taller-global/${ctx.state.taller_global_id}/taller/${data.id}`, data)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    deleteCursoTaller(ctx, id) {
      return new Promise((resolve, reject) => {
        axios
          .delete(`/servicio-deportivo/taller-global/${ctx.state.taller_global_id}/taller/${id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    fetchTallerGlobal(ctx, params) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-cultural-deportivo/${params.codigo}/global/${params.id}`)
          .then(response => resolve(response.data))
          .catch(error => reject(error))
      })
    },
  },

}
