<template>
  <div>
    <!-- Breadcrumb -->
    <!-- <div class="d-flex justify-content-start align-items-center">
      <span class="border-right pr-1 mr-1"><h3>Deportivo</h3></span>
      <feather-icon
        icon="HomeIcon"
        class="text-primary"
        size="16"
      />
      <feather-icon
        icon="ChevronRightIcon"
      />
      <b-breadcrumb
        :items="itemsBreadcrumb"
      />
    </div>
    <br> -->
    <!-- Breadcrumb End -->
    <b-card
      fluid
      class="pb-2"
    >
      <b-row class="border-bottom">
        <b-col
          cols="8"
          lg="8"
          md="6"
          sm="12"
        >
          <h4 class="text-primary pb-1 mt-1">
            <feather-icon
              icon="LifeBuoyIcon"
              size="20"
              class="mr-1"
            />
            <span v-if="codigo === 'OCADE-DEPORTIVO'">TALLER GLOBAL DEPORTIVO:</span>
            <span v-else-if="codigo === 'OCADE-CULTURAL'">TALLER GLOBAL CULTURAL:</span>
            <span v-else-if="codigo === 'OCADE-ALT-COMP'">PROGRAMA DEPORTIVO:</span>
            <b-badge
              variant="light-primary"
              class="px-3 ml-1"
            >
              {{ tallerGlobal_nombre }}
            </b-badge>
          </h4>
        </b-col>
        <b-col
          cols="4"
          lg="4"
          md="6"
          sm="12"
        >
          <b-button
            size="sm"
            variant="primary"
            class="mt-1 px-3 float-right"
            @click="abrirModal"
          >
            <feather-icon icon="PlusCircleIcon" /> Nuevo taller de {{ tallerGlobal_nombre }}
          </b-button>
        </b-col>
      </b-row>
      <br>

      <CursoTallerDeportivoNuevo
        ref="tallerNuevo"
        :action-taller="actionTaller"
        @actToFrom="refreshTabla"
      />

      <b-row>
        <b-col
          cols="7"
          class=""
        >
          <label>Mostrar</label>
          <b-form-select
            id="per-page-select"
            v-model="perPage"
            :options="pageOptions"
            size="sm"
            class="per-page-selector d-inline-block mx-50"
            style="width:auto"
            @change="actualizar_to_from"
          />
          <label>registros</label></b-col>

        <b-col
          cols="5"
          class="mb-1"
        >
          <b-form-group
            label=""
            label-for="filter-input"
            label-cols-sm="3"
            label-align-sm="right"
            class="mb-0"
          >
            <b-input-group>
              <b-form-input
                id="filter-input"
                v-model="filter"
                type="search"
                placeholder="Buscar..."
                size="sm"
              />

              <b-input-group-append>
                <b-button
                  :disabled="!filter"
                  variant="success"
                  size="sm"
                  @click="filter = ''"
                >
                  Limpiar
                </b-button>
              </b-input-group-append>
            </b-input-group>
          </b-form-group></b-col>
      </b-row>
      <b-table
        :items="talleresSegunDeporte"
        :fields="fields"
        :current-page="currentPage"
        :per-page="perPage"
        :filter="filter"
        :filter-included-fields="filterOn"
        :sort-by.sync="sortBy"
        :sort-desc.sync="sortDesc"
        :sort-direction="sortDirection"
        stacked="md"
        head-variant="light"
        class="position-relative"
        show-empty
        small
        hover
        responsive
        :empty-text="'No hay registros para mostrar'"
        :empty-filtered-text="'No hay registros que coincidan con su solicitud.'"
        @filtered="onFiltered"
      >
        <template #cell(nombre)="row">
          <span class="text-primary"><strong>{{ row.item.nombre }}</strong></span>
        </template>
        <template #cell(acciones)="row">
          <b-dropdown
            variant="link"
            no-caret
            :right="$store.state.appConfig.isRTL"
          >
            <template #button-content>
              <feather-icon
                icon="MoreVerticalIcon"
                size="16"
                class="align-middle text-body"
              />
            </template>
            <b-dropdown-item
              @click="editarCursoTaller(row.item)"
            >
              <feather-icon icon="EditIcon" />
              <span class="align-middle ml-50">Editar</span>
            </b-dropdown-item>
            <b-dropdown-item
              :to="{ name: 'ver-estudiantes-taller-deporte', params: { id: row.item.id, tallerGlobal_id: id} }"
            >
              <feather-icon icon="UsersIcon" />
              <span class="align-middle ml-50">Ver estudiantes</span>
            </b-dropdown-item>
            <b-dropdown-item
              :to="{ name: 'ver-actividades-taller-deporte', params: { id: row.item.id, tallerGlobal_id: id } }"
            >
              <feather-icon icon="ListIcon" />
              <span class="align-middle ml-50">Ver actividades</span>
            </b-dropdown-item>
            <b-dropdown-item
              @click="eliminarCursoTaller(row.item)"
            >
              <feather-icon icon="TrashIcon" />
              <span class="align-middle ml-50">Eliminar</span>
            </b-dropdown-item>
          </b-dropdown>
        </template>
        <template #cell(estado)="row">
          <b-badge
            pill
            :variant="`light-${resolveTallerStatusVariant(row.item.estado)}`"
          >
            {{ row.item.estado === 1 ? 'Activo' : 'Inactivo' }}
          </b-badge>
        </template>
      </b-table>
      <b-row
        cols="12"
        class="d-flex justify-content-between mb-1 mb-md-0"
      >
        <b-col cols="6">
          <span class="text-muted">Mostrando registros del {{ from }} al {{ to }} de un total de {{ totalRows }} registros</span>
        </b-col>
        <b-col cols="4">
          <b-pagination
            v-model="currentPage"
            :total-rows="totalRows"
            :per-page="perPage"
            align="fill"
            size="sm"
            class="my-0"
            @input="actualizar_to_from"
          />
        </b-col>

      </b-row>
    </b-card>
  </div>

</template>

<script>
import {
  BCard, BDropdown, BDropdownItem, BBadge, BFormSelect, BButton, BFormGroup, BFormInput, BRow, BCol, BInputGroup, BPagination, BTable, BInputGroupAppend,
} from 'bootstrap-vue'
import store from '@/store'
import { onUnmounted } from '@vue/composition-api'

import talleresStoreModule from '@/views/ocade/talleres-globales/talleres/talleresStoreModule'
import CursoTallerDeportivoNuevo from '@/views/servicio-deportivo/talleres-deportivos/curso-taller/CursoTallerDeportivoNuevo.vue'
import { mapState } from 'vuex'

export default {
  components: {
    CursoTallerDeportivoNuevo,
    BCard,
    BButton,
    BDropdown,
    BDropdownItem,
    BFormGroup,
    BFormInput,
    BRow,
    BCol,
    BInputGroup,
    BFormSelect,
    BPagination,
    BTable,
    BInputGroupAppend,
    BBadge,

  },
  props: {
    codigo: {
      type: String,
      default: '#',
    },
  },
  data() {
    return {
      id: this.$route.params.id,
      cursoTallerUpdate: {},
      tallerGlobal_nombre: '',
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15, { value: 100, text: 'Ver todo' }],
      sortBy: '',
      sortDesc: false,
      sortDirection: 'desc',
      filter: null,
      filterOn: [],
      totalRows: 0,
      to: 0,
      from: 0,
      actionTaller: false,

      // itemsBreadcrumb: [
      //   {
      //     text: 'Talleres Globales',
      //     to: { name: 'servicio-deportivo' },
      //   },
      //   {
      //     text: 'Taller deportivo',
      //     active: true,
      //   },
      // ],
    }
  },
  computed: {
    ...mapState('talleres', ['talleresSegunDeporte', 'tituloModal', 'tituloBoton', 'guardarEditar', 'cursoTallerNuevo']),

  },
  mounted() {
    console.log('ESTOY EN TALLERES:', this.codigo)
    store.dispatch('talleres/fetchTalleres', { codigo: this.codigo, id: this.id }).then(response => {
      store.state.talleres.taller_global_id = this.id
      this.totalRows = response.data.length
      this.actualizar_to_from()
    })

    store.dispatch('talleres/fetchTallerGlobal', { codigo: this.codigo, id: this.id }).then(response => {
      this.tallerGlobal_nombre = response.nombre
    })
  },
  methods: {
    refreshTabla() {
      store.dispatch('talleres/fetchTalleres', { id: this.id }).then(response => {
        if (response !== undefined) {
          this.totalRows = response.data.length
        }

        this.actualizar_to_from()
      })
    },
    actualizar_to_from() {
      const { totalRows } = this
      if (totalRows === 0) {
        this.from = 0
        this.to = 0
      } else
      if (this.currentPage === 1) {
        this.from = 1
        if (totalRows <= this.perPage) {
          this.to = totalRows
        } else {
          this.to = this.perPage
        }
      } else {
        this.from = ((this.currentPage - 1) * this.perPage) + 1
        if ((this.currentPage * this.perPage) <= totalRows) {
          this.to = this.currentPage * this.perPage
        } else {
          this.to = totalRows
        }
      }
    },
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length
      this.currentPage = 1
      this.actualizar_to_from()
    },
    abrirModal() {
      store.state.talleres.tituloModal = 'Nuevo Taller'
      store.state.talleres.tituloBoton = 'Registrar Taller'
      store.state.talleres.guardarEditar = true
      this.$refs.tallerNuevo.cursoTallerNuevo = {}
      this.actionTaller = false
      this.$bvModal.show('modal_nuevo_cursoTaller')
    },
    editarCursoTaller(item) {
      this.actionTaller = true
      this.$refs.tallerNuevo.cursoTallerNuevo.nombre = item.nombre
      this.$refs.tallerNuevo.cursoTallerNuevo.f_inicio = item.f_inicio
      this.$refs.tallerNuevo.cursoTallerNuevo.f_fin = item.f_fin
      this.$refs.tallerNuevo.cursoTallerNuevo.profesor_id = item.profesor_id
      this.$refs.tallerNuevo.cursoTallerNuevo.id = item.id
      this.$refs.tallerNuevo.cursoTallerNuevo.estado = item.estado
      store.state.talleres.tituloModal = 'Editar Taller'
      store.state.talleres.tituloBoton = 'Actualizar'
      store.state.talleres.guardarEditar = false
      this.$bvModal.show('modal_nuevo_cursoTaller')
    },
    eliminarCursoTaller(value) {
      this.$swal({
        title: `Esta seguro de eliminar el taller ${value.nombre}`,
        text: 'Esta acción es irreversible!',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Si, eliminar!',
        customClass: {
          confirmButton: 'btn btn-primary',
          cancelButton: 'btn btn-outline-danger ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          store.dispatch('talleres/deleteCursoTaller', value.id).then(() => {
            this.$swal({
              icon: 'success',
              title: 'Realizado!',
              text: 'Taller eliminado.',
              customClass: {
                confirmButton: 'btn btn-success',
              },
            })
            this.refreshTabla()
          }).catch(error => {
            console.log(error)
          })
        }
      })
    },
  },
  setup() {
    const TALLERES_APP_STORE_MODULE_NAME = 'talleres'
    // Register module
    if (!store.hasModule(TALLERES_APP_STORE_MODULE_NAME)) store.registerModule(TALLERES_APP_STORE_MODULE_NAME, talleresStoreModule)

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(TALLERES_APP_STORE_MODULE_NAME)) store.unregisterModule(TALLERES_APP_STORE_MODULE_NAME)
    })
    const resolveTallerStatusVariant = estado => {
      if (estado === 1) return 'success'
      if (estado === 0) return 'danger'
      return 'primary'
    }
    const fields = [
      { key: 'nombre', label: 'Nombre', sortable: true },
      { key: 'semestre', label: 'Semestre', sortable: true },
      { key: 'profesor_id', label: 'Profesor', sortable: true },
      {
        key: 'f_inicio', label: 'FECHA Inicio', sortDirection: 'desc', sortable: true,
      },
      { key: 'f_fin', label: 'FECHA Fin', sortable: true },
      { key: 'estado', label: 'Estado', sortable: true },
      { key: 'acciones', label: 'acciones' },
    ]
    return {
      fields,
      resolveTallerStatusVariant,
    }
  },
}
</script>
