<template>
  <b-container class="bg-white bordered">
    <b-modal
      id="modal_nuevo_cursoTaller"
      :title="tituloModal"
      ok-only
      :hide-footer="true"
    >
      <form
        ref="form"
        @submit.prevent="guardar_actualizar"
      >
        <b-form-group
          label-for="name-input"
          invalid-feedback="Name is required"
        >
          <label
            for=""
            class="mt-1"
          >Nombre del Taller</label>
          <b-form-input
            id="name-input"
            v-model="cursoTallerNuevo.nombre"
            required
          />
          <label
            for=""
            class="mt-1"
          >Docente</label>
          <b-form-select
            v-model="cursoTallerNuevo.profesor_id"
            :options="profesores"
            required
          />

          <b-form-group
            label="Semestre:"
            label-for="semestre_convocatoria"
            class="mt-1"
          >
            <b-form-select
              id="semestre_convocatoria"
              v-model="cursoTallerNuevo.semestre_id"
              required
              :options="optionsSemestres"
            />

          </b-form-group>
          <b-row>
            <b-col
              cols="6"
            >
              <label
                for=""
                class="mt-1"
              >Fecha Inicio</label>
              <b-form-input
                v-model="cursoTallerNuevo.f_inicio"
                type="date"
                required
              />
            </b-col>
            <b-col
              cols="6"
            >
              <label
                for=""
                class="mt-1"
              >Fecha Fin</label>
              <b-form-input
                v-model="cursoTallerNuevo.f_fin"
                type="date"
                required
              />
            </b-col>
          </b-row>
          <br>
          <b-row v-if="actionTaller">
            <b-col
              cols="3"
            >
              <label
                for=""
                class="mt-1"
              >Estado: </label>
            </b-col>
            <b-col
              cols="9"
            >
              <b-form-radio-group
                v-model="cursoTallerNuevo.estado"
                name="radiobtn"
                class="pt-1"
              >
                <b-form-radio
                  :value="1"
                >
                  ACTIVO
                </b-form-radio>
                <b-form-radio
                  :value="0"
                >
                  INACTIVO
                </b-form-radio>
              </b-form-radio-group>
            </b-col>
          </b-row>
        </b-form-group>
        <br>
        <b-button
          variant="outline-primary"
          size="sm"
          class="float-right"
          type="submit"
        >
          {{ tituloBoton }}
          <feather-icon icon="SaveIcon" />
        </b-button>
      </form>
      <template #modal-footer>
        <div class="w-100">

          <!-- <b-button
            variant="primary"
            size="sm"
            class="float-right"
            @click="guardar_actualizar()"
          >
            {{ tituloBoton }}
          </b-button> -->
        </div>
      </template>
    </b-modal>
  </b-container>
</template>

<script>
import {
  BContainer, BFormSelect, BButton, BFormGroup, BFormInput, BRow, BCol, BFormRadio, BFormRadioGroup,
} from 'bootstrap-vue'
import { mapState } from 'vuex'

import store from '@/store'
import FeatherIcon from '@/@core/components/feather-icon/FeatherIcon.vue'

export default {
  components: {
    BButton,
    BFormGroup,
    BFormInput,
    BContainer,
    BFormSelect,
    BRow,
    BCol,
    BFormRadio,
    BFormRadioGroup,
    FeatherIcon,
  },
  props: {
    actionTaller: {
      type: Boolean,
      required: true,
    },
  },
  data() {
    return {
      optionsSemestres: [],
      cursoTallerNuevo: {
        nombre: '',
        profesor_id: '',
        f_inicio: '',
        f_fin: '',
        taller_global_id: '',
        estado: true,
      },
      profesores: [
        { value: '1001', text: 'Hilberto Castillo Terrones' },
        { value: '1002', text: 'Pedro Panduro Puente' },
        { value: '1003', text: 'Carlos Roberto Ramirez Remigio' },
        { value: '1004', text: 'Maria FErnandez Maldonado Chavez' },
        { value: '1005', text: 'Pedro Grau Villavicencios' },
        { value: '1006', text: 'Jacobo Meza Bueno' },
        { value: '1007', text: 'Elias Cabello Contreras' },
        { value: '1008', text: 'Leonardo Garcia Martinez' },
      ],
    }
  },
  computed: {
    ...mapState('talleres', ['tituloModal', 'tituloBoton', 'guardarEditar']),
  },
  mounted() {
    this.$http.get('/servicio-social/beca/show-semestres').then(res => {
      this.optionsSemestres = res.data.map(value => ({
        value: value.id, text: value.nombre,
      }))
    })
  },
  methods: {

    guardar_actualizar() {
      if (store.state.talleres.guardarEditar) { // true = guardar
        const data = this.cursoTallerNuevo
        // eslint-disable-next-line no-unused-vars
        this.cursoTallerNuevo.taller_global_id = store.state.talleres.taller_global_id
        store.dispatch('talleres/addCursoTaller', data).then(() => {
          this.$swal({
            position: 'top-end',
            icon: 'success',
            title: 'Taller registrado!!',
            showConfirmButton: false,
            timer: 1500,
            customClass: {
              confirmButton: 'btn btn-primary',
            },
            buttonsStyling: false,
          })

          this.$emit('actToFrom')
          this.$bvModal.hide('modal_nuevo_cursoTaller')
        }).catch(error => {
          console.log(error)
        })
      } else {
        store.dispatch('talleres/updateCursoTaller', this.cursoTallerNuevo).then(() => {
          this.$swal({
            position: 'top-end',
            icon: 'success',
            title: 'Taller actualizado!!',
            showConfirmButton: false,
            timer: 1500,
            customClass: {
              confirmButton: 'btn btn-primary',
            },
            buttonsStyling: false,
          })
          this.$emit('actToFrom')
          this.$bvModal.hide('modal_nuevo_cursoTaller')
        }).catch(error => {
          console.log(error)
        })
      }
    },
  },
}
</script>

<style lang="scss">
@import '@core/scss/vue/libs/vue-flatpicker.scss';
</style>
